.mobile {
  width: 70%;
  transition: 0.3s ease;
}
.drawer {
  display: none;
}
.navigation-hamburger {
  display: none;
}
@media screen and (max-width: 1600px) {
  .mobile {
    width: 65%;
  }
}
@media screen and (max-width: 1500px) {
  .mobile {
    width: 75%;
  }
}
@media screen and (max-width: 1300px) {
  .contacts-grid-item-important {
    padding-top: 10rem;
  }
  .navigation-item {
    display: none;
  }
  .drawer {
    display: flex !important;
  }
  .navigation-hamburger {
    display: inline-block !important;
  }
  .navigation__logo-container {
    display: flex;
    justify-content: center;
    text-align: start;
    align-items: center !important;
  }
  .navigation__logo-container h1 {
    text-align: start;
    font-size: 2rem;
  }
  .navigation__logo-container p {
    text-align: start;
    display: none;
  }
  .welcome-hero h1 {
    font-size: 4rem;
    width: 90%;
    margin-bottom: 3rem;
  }
  .mobile {
    width: 85%;
  }
  .main-image-container {
    grid-column: 1/-1;
  }
  .main-content-container {
    grid-gap: 5rem;
  }
}
@media screen and (max-width: 1100px) {
  .main-content__title {
    font-size: 6rem !important;
  }
  .mobile {
    width: 90%;
  }
  .hotel-page-hero img {
    height: 150%;
    margin-top: 0rem !important;
  }
  .rooms-grid-big {
    grid-column: 1/-1;
  }
  .rooms-grid-small {
    grid-column: 1/-1;
  }
  .main-content-text-hero h1 {
    font-size: 2rem;
  }
  .contacts-grid {
    grid-template-columns: 1fr !important;
    grid-gap: 5rem;
  }
  .contacts-grid-item {
    grid-column: 1/3;
    text-align: left;
  }
  .contacts-grid-item1 {
    grid-column: 1/-1;
    grid-row: auto;
    text-align: left;
  }

  .contacts-grid-item-important {
    text-align: left;
    margin-top: 4rem;
    grid-column: 1/-1;
  }
}

@media screen and (max-width: 800px) {
  .grid {
    grid-template-columns: 100%;
  }
  .item {
    padding: 2rem 0rem !important;
  }
  .grid-item3-4 {
    grid-column: 1/2;
    padding: 2rem 0rem !important;
  }
  .main-content__title {
    font-size: 4rem !important;
  }
  .book-up-container {
    flex-direction: column;
    align-items: center;
    overflow-x: hidden;
  }
  .welcome-hero {
    width: 100% !important;
  }
  .book-input {
    width: 100% !important;
    overflow-x: hidden !important;
  }
  .book-input-number {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
  .welcome-item-text h2 {
    text-align: start;
    font-size: 3rem;
  }
  .welcome-item {
    align-items: flex-start;
  }
  .welcome-item-text button {
    margin-left: 0;
    justify-self: flex-start;
    align-self: flex-start;
    margin-right: auto;
  }
  .book-input h1 {
    text-align: start !important;
    font-size: 3rem !important;
    margin-bottom: 1rem !important;
  }
  .book-input-button {
    width: 100% !important;
    margin-top: 4rem;
  }
  .book-up-container div {
    width: 100%;
    overflow-x: hidden;
  }
  .navigation {
    width: 90% !important;
  }
  .book-input-number {
    width: 100%;
  }
  .tavern-grid-item-big {
    grid-column: 1/-1;
  }
  .welcome-hero h1 {
    font-size: 4rem;
    width: 100%;
    margin-bottom: 3rem;
    text-align: start;
  }
  .welcome-hero {
    align-items: flex-start;
  }
  .welcome-hero-container {
    align-items: center;
  }
  .welcome-hero p {
    display: none;
  }
  .img-cont {
    width: 100%;
  }
  .tavern-grid-item-small {
    grid-column: 1/-1;
  }
  .mobile {
    width: 90%;
  }
  .main-content-text-hero h1 {
    font-size: 3rem;
  }
  .grid-item1-3 {
    grid-column: 1/-1;
  }
  .grid-item3-4 {
    grid-column: 1/-1;
  }
  .footer-container {
    grid-column: 1/-1;
  }
  .footer-maps {
    height: 60rem;
  }
  .footer-grid {
    grid-gap: 2rem;
  }

  .around-us-grid-item-big {
    grid-column: 1/-1;
  }
  .around-us-grid-item-small {
    grid-column: 1/-1;
  }
}
