.reservation-item {
  width: 30rem;
  margin-top: 2rem;
}
.reservation-item h1 {
  margin-bottom: 1rem;
  font-weight: 500;
  font-size: 1.5rem;
}
.reservation-item input {
  width: 100%;
  padding: 1.5rem 0;
}
.reservation {
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.reservation-grid {
  margin-top: 20rem;
  margin-bottom: 0rem;
  grid-gap: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.reservation-grid h5 {
  background: linear-gradient(217deg, #008cff, #c300ff);
  -webkit-text-stroke: 0.5px #008cff;
  background-clip: text;
  color: transparent;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.reservation-grid-input {
  background: #2e2c31;
  border: none;
  height: 40px;
  color: #008cff;
  padding: 1rem 0rem;
  width: 100%;
}
.reservation-grid-input::placeholder {
  color: #008cff;
}
.reservation-grid input {
  color: #008cff !important;
}
.react-date-picker {
  width: 100% !important;
}
