.book-input {
  width: fit-content;
  text-align: left;
  color: #3da8ff;
  font-weight: 300;
  overflow: hidden;
}
.book-input svg {
  color: #ffecb3 !important;
}
.book-input h1 {
  font-size: 5rem;
  margin-bottom: 70px;
  text-align: center;
  color: transparent;
  background: linear-gradient(217deg, #ff00d4, #008cff);
  background-clip: text;
}
.book-input input {
  background: #323136bd;
  height: 40px;
  font-size: 1.2rem;
  color: #3da8ff;
  border: none !important;
}
.react-date-picker__wrapper {
  border: none;
  border: none !important;
  height: 40px;
  margin: 0 !important;
}
.react-date-picker__inputGroup {
}
.book-input input svg {
  color: #3da8ff !important;
}
.react-date-picker__button {
  color: #3da8ff !important;
}
.react-date-picker__button svg {
  color: #3da8ff !important;
  stroke: #3da8ff;
}
.react-date-picker__clear-button {
  color: #3da8ff !important;
}

.react-date-picker {
  color: #5099ff;
  background: #323136bd;
  border: none !important;
  outline: none;
  margin: 0 !important;
  border-radius: 3px;
  padding: 1rem;
}
.react-date-picker input {
  background: transparent;
  border: none !important;
}
.react-date-picker__inputGroup__year {
  width: 32px !important;
}
.react-date-picker__inputGroup__day {
  width: 18px !important;
}

.react-calendar {
  background: #323136bd;
  color: #3da8ff !important;
}
.react-calendar__month-view__days__day {
  color: #3da8ff !important;
}
.react-calendar__month-view__days__day--weekend {
  color: #ff00d4 !important;
}
.book-input-button {
  height: 61px;
  width: 110px;
  background: linear-gradient(217deg, #ff00d4, #008cff);
  border: 1px solid #654bd8;
  color: white;
  font-weight: 1000;
  text-transform: uppercase;
  padding: 1rem;
  transition: 0.3s ease;
  cursor: none;
}
.book-input-button:hover {
  opacity: 0;
}
.book-input-number {
  height: 40px;
  padding: 1rem;
}
.book-up-container {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  justify-content: center;
}
.book-up-container div {
  margin: 0.2rem;
}
