.video-background-container video {
  min-width: 100%;
  object-fit: cover;
  height: 95%;
  opacity: 0.2;
  position: absolute;
}
.video-background-container {
  top: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 88vh;
  position: absolute;
}
.video-background-background {
  display: flex;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: radial-gradient(rgba(255, 255, 255, 0), rgba(50, 48, 56, 0.253));
  position: absolute;
  justify-content: space-evenly;
}
.video-background-background div {
  height: 100%;
  width: 0.5px;
  background: rgba(0, 0, 0, 0.164);
}
.asasd {
  background: #ffe8a36b;
}
