.scroller {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  box-shadow: 0px 0px 5px 0px #bbc5f0;

  position: fixed;
  z-index: 9990;
  pointer-events: none;
}
.scroller-content {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999 !important;
  pointer-events: none;
  position: absolute;
}
.scroller-dot {
  height: 15px;
  width: 15px;
  position: fixed;
  background: linear-gradient(217deg, #ff00d4, #008cff);
  pointer-events: none;
  border-radius: 100%;
  z-index: 9999 !important;
  box-shadow: 0px 0px 3px 0px #7c8fe7;
}
.scroller-content-item {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cursor-item-sended {
  font-size: 1.5rem;
  display: flex;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 1000;
  white-space: nowrap;
  position: relative;
  height: 140px;
  min-height: 140px !important;
  min-width: 140px;
  width: 140px;
}
.cursor-item-sended svg {
  font-size: 4rem;
  pointer-events: none;
  position: absolute;
  pointer-events: none;
  height: 140px;
  min-height: 140px !important;
  min-width: 140px;
}
.cursor-item-sended img {
  border-radius: 100%;
  height: 140px;
  min-height: 140px !important;
  min-width: 140px;
  width: 140px;
  object-fit: cover;
  position: absolute;
  pointer-events: none;
  animation: cursor-img ease-in-out 0.3s forwards;
}

.cursor-item-sended span {
  z-index: 100;
  background: linear-gradient(217deg, #ff00d4, #008cff);
  background-clip: text;
  font-weight: 1000;
  font-size: 4rem;
  -webkit-text-stroke: 0.5px rgb(71, 71, 71);
  animation: cursor-img ease-in-out 0.3s forwards;
  pointer-events: none;
  position: absolute;
  pointer-events: none;
}
@keyframes cursor-img {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
