.hotel-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
}
.hotel-page h1 {
  background: linear-gradient(217deg, #ff00d4, #008cff);
  background-clip: text;
  color: transparent;
  width: fit-content;
  font-weight: 1000;
  font-size: 3rem;
  margin-bottom: 1rem;
}
.hotel-page p {
  padding: 2rem;
  margin-block-start: 0em;
  margin-block-end: 0em;
  text-align: start;
  white-space: normal;
  font-style: italic;
  font-size: 1.4rem;
  color: #d4d4d4;
  font-weight: 200;
  border-radius: 2px;
  pointer-events: none;
  background: rgba(128, 128, 128, 0.329);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.37);
  color: #dbdbdb;
  opacity: 0.8;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.486);
}
.hotel-page-hero {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25rem;
  overflow: hidden !important;
  background: radial-gradient(rgba(0, 0, 0, 0.329), black);
  position: relative;
}
.hotel-page-hero img {
  width: 100%;
  object-fit: cover;
  position: absolute;
  opacity: 0.01;
  margin-top: -25rem;
}
.hotel-appriciate {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  min-width: 250px;
  width: stretch;
}
.hotel-appriciate li {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
  width: 100%;
}
.hotel-appriciate li span {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 1rem;
  margin: 0.1rem;
  background: rgba(128, 128, 128, 0.329);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.37);
  height: stretch;
  white-space: nowrap;
  color: #008cff;
}
.hotel-appriciate li span svg {
  margin-right: 1rem;
  font-size: 2rem;
  color: #ff00d4;
}
.grid {
  margin-top: 10rem;
  margin-bottom: 10rem;
  grid-gap: 20rem 5rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
}
.grid-item1-3 {
  grid-column: 1/2;
  display: flex;
  flex-direction: column;
}
.grid-item3-4 {
  flex-direction: column;
  display: flex;
  grid-column: 2/3;
  width: 100%;
}

.grid-item1-4 {
  grid-column: 1/-1;
  width: 100%;
}
.item {
  padding: 2rem 2rem;
}
