.welcome-hero {
  z-index: 100;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  width: 95%;
  pointer-events: none;
}
.asdads {
  color: rgba(255, 255, 255, 0);
}
.welcome-hero-container {
  display: flex;
  align-items: center;
  flex: 1;
  width: 100%;
  overflow: hidden;
  height: 100vh !important;
  min-height: 100vh;
  overflow: visible;
  justify-content: center;
}
.welcome-hero h1 {
  pointer-events: none;
  font-size: 7rem;
  color: transparent;
  background: linear-gradient(217deg, #ff00d4, #008cff);
  background-clip: text;
  text-align: center;
  margin-block-end: 0;
  font-weight: 1000;
  letter-spacing: 2px;
  pointer-events: none;
  white-space: normal;
  width: 60%;
  -webkit-text-stroke: 1px #008cff;
}

.welcome-hero p {
  margin-top: 4rem;
  margin-bottom: 4rem;
  width: 50%;
  text-align: center;
  white-space: normal;
  font-style: italic;
  font-size: 1.4rem;
  font-weight: 200;
  padding: 1rem;
  pointer-events: none;
  background: rgba(128, 128, 128, 0.568);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.37);
  color: #dbdbdb;
  padding: 2rem;
  box-shadow: 0px 0px 10px 0px black;
}
.welcome-hero__buttons button {
  margin-top: 3rem;
  background: linear-gradient(217deg, #008cff, #ff00d4);
  border: none;
  color: #ffecb3;
  font-weight: 100;
  outline: none;
  font-size: 1.1rem;
  padding: 1.3rem;
  border-radius: 50px;
  text-transform: uppercase;
  margin: 0.5rem;
  opacity: 0.9;
  border: 1px solid #654bd8;
  font-weight: 1000;
  pointer-events: auto;
  z-index: 9000;
}
.welcome-hero__buttons button:last-child {
  font-weight: 1000;
  border: 1px solid #654bd8;
  color: #654bd8;
  background: transparent;
}
.welcome-hero-button h1 {
  margin-top: 1rem;
  font-size: 1rem !important;
  font-weight: 100 !important;
  -webkit-text-stroke: 0px;
}
.welcome-items {
  margin-top: 3rem;
  display: flex;
  align-items: center;
  flex: 1;
  z-index: 500;
  justify-content: space-between;
  flex-direction: column;
  width: 90%;
}
.welcome-item {
  width: 100%;
  margin: 0.5rem;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column-reverse;
  flex: 1;
}

.welcome-item h2 {
  font-weight: 1000;
  font-size: 4rem;
  color: transparent;
  letter-spacing: 5px;
  margin-block-start: 0em;
  white-space: normal;
  background: linear-gradient(217deg, #ff00d4, #008cff);
  background-clip: text;
}
.welcome-item p {
  width: 100%;
  text-align: start;
  white-space: normal;
  font-size: 1.3rem;
  font-style: italic;
  font-weight: 300;
  margin-block-start: 0em;
  margin-block-end: 0em;

  pointer-events: none;
  background: #323136dc;
  padding: 2.9rem;
  box-shadow: 0px 0px 10px 0px black;
  background: rgba(128, 128, 128, 0.329);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.37);
  color: #dbdbdb;
  height: 100%;
}
.welcome-item img {
  object-fit: cover;
  opacity: 1;
  box-shadow: 0px 0px 30px 0px black;
  width: 300px;
  margin: 0.5rem;
  height: 200px;
}
.welcome-item-text {
  padding: 0rem;
  flex: 1;
}
.welcome-item-text button {
  padding: 1.5rem;
  margin-top: 0rem;
  width: 100px;
  font-weight: 1000;
  font-size: 1.3rem;
  border: 0.5px solid #9c8e65;
  background: linear-gradient(217deg, #ff00d4, #008cff);
  border-radius: 2px;
  color: white;
  border: 1px solid #654bd8;
  border-radius: 50px;
  margin: auto;
}
.welcome-footer {
  height: 15vh;
  width: 100%;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 100;
  color: #ffecb3;
  background: #212024;
  box-shadow: 0px -5px 15px 0px #000000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.welcome-footer h4 {
  font-weight: 100;
}
.welcome-item-text-cont {
  display: flex;
  height: fit-content;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: center;
}
.welcome-item-text-cont p {
  flex: 1;
  min-width: 250px !important;
}
