.around-us {
  display: flex;
  width: 100%;
  align-items: center;
  flex-direction: column;
}
.around-us h1 {
  background: linear-gradient(217deg, #ff00d4, #008cff);
  background-clip: text;
  color: transparent;
  width: fit-content;
  font-weight: 1000;
  font-size: 3rem;
}
.around-us p {
  text-align: start;
  white-space: normal;
  font-size: 1.4rem;
  color: #dbdbdb;
  font-weight: 300;
  border-radius: 2px;
  pointer-events: none;
  background: rgba(128, 128, 128, 0.329);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.37);
  font-style: italic;
  color: #dbdbdb;
  padding: 2rem;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.486);
  margin-block-start: 0em;
  margin-block-end: 0em;
}

.around-us-grid {
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: grid;
  grid-gap: 20rem 5rem;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}
.around-us-grid-item-big {
  position: relative;
  grid-column: 1fr;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.around-us-grid-item-small {
  grid-column: 1fr;
  position: relative;
  display: flex;
  align-items: center;
  width: stretch;
  height: 100%;
}
.around-us-grid-item-small div {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  margin-top: 3rem;
}
