.drawer {
  position: fixed;
  top: 0%;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #212024;
  z-index: 8999;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.3rem;
  font-weight: 1000;
  display: none;
}
.drawer ul li {
  padding: 1rem;
  margin: 1rem;
  background: linear-gradient(217deg, #008cff, #c300ff);
  -webkit-text-stroke: 0.5px #008cff;
  background-clip: text;
  color: transparent;
}
.hamburger-react div {
  background: linear-gradient(217deg, #008cff, #c300ff) !important;
}
