.navigation-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: #212024;
  box-shadow: 0px 0px 15px 0px #000000;
  z-index: 9000;
  height: 12vh;
  position: sticky;
  top: 0;
  background-image: url("/public/background.jpg");
  overflow: overlay;
}

.navigation {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
  z-index: 9900;
}

.navigation__logo-container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex: 1;
  height: 8rem;
  overflow: hidden;
  width: fit-content;
  flex-direction: column;
  color: #63a4ff;
  min-width: 300px;
}
.navigation__logo-container h1 {
  width: 100%;
  font-weight: 1000;
  font-size: 4rem;
  margin-block-start: 0em;
  margin-block-end: 0em;
  color: transparent;
  -webkit-text-stroke: 1px #5099ff;
  text-align: center;
  background: linear-gradient(217deg, #008cff, #ff00d4);
  background-clip: text;
}
.navigation__logo-container p {
  width: 100%;
  margin-top: -0.5rem;
  color: #2271e3;
  text-align: center;
  letter-spacing: 4px;
}
.navigation-item {
  flex-direction: column;
  width: fit-content;
  text-align: center;
  width: 150px;
  padding: 1.5rem 0rem;
  margin: 1.5rem;
  font-size: 1.4rem;
  font-weight: 300;

  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
  color: rgba(255, 255, 255, 0.253);

  opacity: 1 !important;
  background: linear-gradient(#008cff, #c300ff);
  background-clip: text;

  border-top: none;
  border-left: none;
  border-right: none;
  min-width: 100px;
}
.navigation-item:hover {
  opacity: 0;
  display: hidden;
  color: transparent;
  background: transparent;
}
.navigation-item svg {
  font-size: 2rem;
  color: transparent;
  opacity: 1;
  margin-bottom: 0.6rem;
  overflow: overlay;
  z-index: -1;
  display: none;
}
.navigation-item:first-child {
  margin-left: 0;
  padding-left: 0;
}
.navigation-item:last-child {
  margin-right: 0;
}
